.App {
    text-align: center;
}

.black-nav {
    display: flex;
    background: black;
    width: 100%;
    color: white;
    padding-left: 20px;
}

div {
    box-sizing: border-box;
}

.aaa {
    margin-top: 20px;
    padding: 20px;
    background: #eee;
    text-align: left;
}

.list {
    padding-left: 20px;
    text-align: left;
    border-bottom: 1px solid gray;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
